.ce-chat-card {
    margin-bottom: 0.5rem;
    border-radius: 1rem;
}
.ce-active-chat-card {
    background-color: rgb(20, 72, 121) !important;
    box-shadow: 4px 4px 8px 8px rgba(0, 0, 0, 0.2),
        6px 6px 20px 10px rgba(0, 0, 0, 0.19);
    color: white !important;
}
/* .ce-chats-container {
  background-color: rgba(248, 246, 246, 0.63) !important;
} */
.ce-chat-form-container,
.ce-chats-container,
.ce-settings {
    background-color: rgba(248, 246, 246, 0.63) !important;
}
.ce-chat-card.false {
    background-color: rgb(213, 220, 233) !important;
}
#new-chat-plus-button.ce-primary-button {
    background-color: rgb(20, 72, 121) !important;
}
.ce-chat-title-container {
    background-color: rgba(213, 220, 233, 0.637) !important;
    color: rgb(20, 72, 121) !important;
}
.ql-editor {
    background-color: rgba(213, 220, 233, 0.726) !important;
}
.ce-message-bubble.ce-my-message-bubble {
    background-color: rgb(20, 72, 121) !important;
}
#ce-send-message-button {
    background-color: rgb(20, 72, 121) !important;
}
.ce-input.ce-text-input {
    background-color: rgb(213, 220, 233);
    color: rgb(20, 72, 121);
}

